.btn {
  position: relative;
  display: inline-block;
  padding: 10px 15px;
  vertical-align: top;
  text-align: center;
  border: solid 1px #dfdfdf;
  background-color: #fff;
  font-weight: 500;
  font-size: 13px;
  border-radius: 4px;
  transition: all 0.2s ease-in-out;
  cursor: pointer;
}

.btn:hover {
  color: #ea2b16;
}

.btn:focus {
  outline: none;
  border-color: #ea2b16;
  cursor: pointer;
}

.btn--icon {
  display: flex;
  align-items: center;
}

.btn--location, .btn--user, .btn--star {
  padding: 0;
  border: none;
  background-color: transparent;
  letter-spacing: 0.2px;
  color: #767676;
}

.btn--location:hover span, .btn--user:hover span, .btn--star:hover span {
  border-bottom-color: transparent;
}

.btn--location .icon, .btn--user .icon, .btn--star .icon {
  margin-right: 12px;
  fill: #767676;
}

.btn--location span, .btn--user span, .btn--star span {
  border-bottom: 1px dashed #767676;
}

.btn--user, .btn--star {
  color: #fff;
}

.btn--user .icon, .btn--star .icon {
  fill: #767676;
  margin-right: 11px;
}

.btn--user span, .btn--star span {
